<script>
export default {
  data: function() {
    return {
      sliding: null,
      sliderSpeed: 50,
      title:
        "&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events&nbsp;&nbsp;Crazy about events",
      actualSlide: 0,
      items: [
        // {
        //   tag: "crazyAboutEvents",
        //   bg: "",
        //   background: "#ffffff",
        //   imagesUnderText: ["1.png", "2.png", "3.png"],
        //   imagesOverText: ["6.png"]
        // },
        // {
        //   tag: "courier",
        //   bg: "bg.png",
        //   background: "#ffffff",
        //   imagesUnderText: [],
        //   imagesOverText: ["1.png"]
        // },
        {
          tag: "stop",
          bg: "bg.png",
          background: "#ffffff",
          imagesUnderText: [],
          imagesOverText: ["1.png"]
        },
        {
          tag: "bridge",
          bg: "bg.png",
          background: "#ffffff",
          imagesUnderText: [],
          imagesOverText: ["1.png"]
        },
        {
          tag: "car",
          bg: "bg.png",
          background: "#ffffff",
          imagesUnderText: [],
          imagesOverText: ["1.png"]
        },
        {
          tag: "dance",
          bg: "bg.png",
          background: "#ffffff",
          imagesUnderText: [],
          imagesOverText: ["1.png"]
        },
        {
          tag: "race",
          bg: "bg.png",
          background: "#ffffff",
          imagesUnderText: [],
          imagesOverText: ["1.png"]
        }
      ]
    };
  },
  methods: {
    moveSlider(val, pos, manual) {
      this.$refs.itemsList.classList.add("posTo" + pos);

      if (manual) {
        clearInterval(this.sliding);
        this.autoSlide();
      }

      // setTimeout(() => {
      if (this.actualSlide === 0 && val === -1) {
        this.actualSlide = this.items.length - 1; // loop: set to last slide
      } else if (this.actualSlide === this.items.length - 1 && val === 1) {
        this.actualSlide = 0; // loop: set to first slide
      } else {
        this.actualSlide = this.actualSlide + val; // default
      }
      // setTimeout(() => {
      this.$refs.itemsList.classList.remove("posTo" + pos);
      // }, 500);
      // }, 500);
    },
    autoSlide() {
      this.sliding = setInterval(() => {
        this.moveSlider(1, "Right", false);
      }, this.sliderSpeed);
    }
  },
  created() {
    this.autoSlide();
  },
  mounted() {
    setTimeout(() => {
      this.sliderSpeed = 6000;
      clearInterval(this.sliding);
      this.autoSlide();
    }, this.items.length * this.sliderSpeed);
  },
  destroyed() {
    clearInterval(this.sliding);
  }
};
</script>

