<template>
  <section id="sectionEvents" class="section eventsList">
    <div class="container container--md">
      <div class="head">
        <h2>PORTFOLIO</h2>
        <h3 class="title-md">the book of love</h3>
      </div>
    </div>
    <div class="relative">
      <i class="iconSlide iconSlide__left" @click="moveSlider(-1)" />
      <div class="item__list relative d-flex" :style="sliderStyles">
        <router-link
          class="item__single d-flex"
          :to="{
            name: 'event',
            params: { slug: `${item[0].dir}` }
          }"
          v-for="(item, index) in events"
          :key="index"
          :style="singleStyles"
        >
          <figure class="item__photo">
            <img
              :src="
                require('' + '@/images/eventsList/' + item[0].dir + '/1.jpg')
              "
            />
          </figure>
          <div class="content">
            <div>
              <h4 class="content__title">{{ item[0].title }}</h4>
            </div>
          </div>
        </router-link>
      </div>
      <i class="iconSlide iconSlide__right" @click="moveSlider(1)" />
    </div>
  </section>
</template>

<script>
import mixins from "@/mixins";
import events from "@/mixins/events.js";
export default {
  data: function() {
    return {
      actualSlide: 0,
      eventsLength: 16
    };
  },
  computed: {
    sliderStyles() {
      return {
        transform:
          "translateX(" + -(this.actualSlide * 100) / this.windowDivide + "vw)",
        width: (this.eventsLength / (this.windowDivide * 2)) * 100 + "vw",
        height: 200 / this.windowDivide + "vw"
      };
    },
    singleStyles() {
      return {
        width: 100 / this.windowDivide + "vw",
        height: 100 / this.windowDivide + "vw"
      };
    }
  },
  methods: {
    moveSlider(val) {
      if (this.actualSlide === 0 && val === -1) {
        this.actualSlide = this.eventsLength / 2 - this.windowDivide; // loop: set to last slide
      } else if (
        this.actualSlide === this.eventsLength / 2 - this.windowDivide &&
        val === 1
      ) {
        this.actualSlide = 0; // loop: set to first slide
      } else {
        this.actualSlide = this.actualSlide + val; // default
      }
    }
  },
  mixins: [mixins, events]
};
</script>

