<template>
  <section class="section singleEvent">
    <router-link
      class="link iconSlide iconSlide__left"
      v-if="getId !== 0"
      :to="{
        name: 'event',
        params: {
          slug: `${events[Object.keys(events)[getId - 1]][0].dir}`
        }
      }"
    />
    <div class="row flex-row-reverse rowContent">
      <div class="textsSection d-flex align-items-end" ref="textsSection">
        <div class="content">
          <h2 class="title-md content__title">{{ getEvent.title }}</h2>

          <div class="content__description">
            <p>{{ getEvent.description }}</p>
            <span class="clickTxt d-md-none d-block"
              >(Kliknij, aby zobaczy cae zdjcie)</span
            >
          </div>
        </div>
      </div>
      <div class="photosSection" ref="photosSection">
        <div>
          <div>
            <figure
              class="photo photo__big"
              @click="() => showImg(actualPhoto)"
            >
              <img
                ref="photoBig"
                :src="
                  require('' +
                    '@/images/eventsList/' +
                    getEvent.dir +
                    '/' +
                    actualPhoto +
                    '.jpg')
                "
              />
            </figure>
          </div>
          <div class="photo__container relative">
            <div class="photo__list relative" ref="photoList">
              <figure
                v-for="(item, index) in getEvent.images"
                :key="index"
                class="photo photo__small"
                @click="changePhoto(index)"
              >
                <div
                  :class="actualPhoto - 1 === index ? 'active' : ''"
                  :datasrc="
                    require('' +
                      '@/images/eventsList/' +
                      getEvent.dir +
                      '/' +
                      (index + 1) +
                      '.jpg')
                  "
                ></div>
              </figure>
            </div>
          </div>
        </div>
      </div>
    </div>
    <vue-easy-lightbox
      :index="index - 1"
      :visible="visible"
      :imgs="generatedImgs"
      @hide="handleHide"
    ></vue-easy-lightbox>
    <router-link
      class="link iconSlide iconSlide__right"
      v-if="getId !== 15"
      :to="{
        name: 'event',
        params: {
          slug: `${events[Object.keys(events)[getId + 1]][0].dir}`
        }
      }"
    />
  </section>
</template>

<script>
import Vue from "vue";
import Lightbox from "vue-easy-lightbox";

Vue.use(Lightbox);
import mixins from "@/mixins";
import events from "@/mixins/events";
export default {
  data: function() {
    return {
      index: 0,
      actualSlide: 0,
      actualPhoto: 1,
      visible: false,
      generatedImgs: []
    };
  },

  methods: {
    changePhoto(index) {
      this.$refs.photoBig.classList.add("changePhoto");
      setTimeout(() => {
        this.actualPhoto = index + 1;
        setTimeout(() => {
          this.$refs.photoBig.classList.remove("changePhoto");
        }, 250);
      }, 250);
    },
    showImg(index) {
      this.index = index;
      this.visible = true;
    },
    handleHide() {
      this.visible = false;
    },
    generateImgs() {
      this.generatedImgs = [];
      let i;
      let filteredList = this.$refs.photoList.childNodes;
      for (i = 0; i < this.getEvent.images; i++) {
        this.generatedImgs.push(
          filteredList[i].children[0].attributes.datasrc.nodeValue
        );
      }
    },
    enterAnimation() {
      this.$refs.photosSection.classList.add("starting");
      this.$refs.textsSection.classList.add("starting");
      setTimeout(() => {
        this.$refs.photosSection.classList.remove("starting");
        this.$refs.textsSection.classList.remove("starting");
      }, 250);
    }
  },

  watch: {
    $route(to, from) {
      this.enterAnimation();
      setTimeout(() => {
        this.generateImgs();
      }, 500);
    }
  },

  computed: {
    getEvent() {
      return this.events[this.$route.params.slug][0];
    },
    getId() {
      return this.events[Object.keys(this.events)[this.getEvent.id]][0].id;
    }
  },
  mounted() {
    this.enterAnimation();
    this.generateImgs();
  },
  mixins: [mixins, events]
};
</script>

