<script>
import Vue from "vue";
import VueScrollTo from "vue-scrollto";

Vue.use(VueScrollTo);
export default {
  data: function() {
    return {
      activeMenu: false
    };
  },
  methods: {
    toggleMenu() {
      this.activeMenu = !this.activeMenu;
    },
    scrollToSection(section) {
      this.$router.push("/");
      setTimeout(() => {
        VueScrollTo.scrollTo(section, 800, { offset: -90 });
      }, 300);
    },
    closeMenu() {
      this.activeMenu = false;
    }
  }
};
</script>

